import React, { useEffect, useState } from 'react';
import './Profile.css';
import { Link } from 'react-router-dom';

function Profile() {

  useEffect(() => {
    document.title = "My Profile"; // setting the page's title
  }, []);

  const [bgColor, setBgColor] = useState('#191a1a');
  const [modeimg, setmode] = useState(process.env.PUBLIC_URL + '/sun1.png');
  const [modeimg1, setmode1] = useState(process.env.PUBLIC_URL + '/profileday.png');
  const [home, sethome] = useState('white');
  const [btncol, setbtncol] = useState('white');
  const [mid, setmid] = useState('#242525');

  const handleClick = () => {
    setBgColor(bgColor === '#191a1a' ? 'white' : '#191a1a');
    setmode(modeimg === process.env.PUBLIC_URL + '/sun1.png' ? process.env.PUBLIC_URL + '/moon1.png' : process.env.PUBLIC_URL + '/sun1.png');
    setmode1(modeimg1 === process.env.PUBLIC_URL + '/profileday.png' ? process.env.PUBLIC_URL + '/profileday.png' : process.env.PUBLIC_URL + '/profileday.png');
    sethome(home === 'white' ? 'black' : 'white');
    setbtncol(btncol === 'white' ? 'black' : 'white');
    setmid(mid === '#242525' ? '#3f5469' : '#242525');
  };

  const [isHovered5, setIsHovered5] = useState(false);
  const [isHovered6, setIsHovered6] = useState(false);

  const homebtn = {
    position: 'absolute',
    top: '18px',
    left: '18px',
    height: '40px',
    width: '185px',
    opacity: isHovered5 ? '0.8' : '0.9'
  };

  const modebtn = {
    position: 'absolute',
    top: '18px',
    right: '18px',
    height: '45px',
    width: '45px',
    cursor: 'pointer',
    opacity: isHovered6 ? '0.8' : '0.9'
  };

  const prof = {
    height: '140px',
    width: '140px',
    cursor: 'pointer',
  }


  return (
    <div id="body1" style={{ backgroundColor: bgColor }}>
      <Link to="/fdquiz">
        <div id="homediv" style={homebtn} onMouseEnter={() => setIsHovered5(true)} onMouseLeave={() => setIsHovered5(false)}>
          <span id="spanx1">fd</span>
          <span id="spanx2" style={{ color: home }}>.quiz</span>
        </div>
      </Link>

      <img src={modeimg} style={modebtn} alt="SetDayMode" onMouseEnter={() => setIsHovered6(true)} onMouseLeave={() => setIsHovered6(false)} onClick={handleClick} />

      <div id="containerpr">
        <div id="containmid" style={{ backgroundColor: mid }}>
          <div className="profile-photo">
            <img src={process.env.PUBLIC_URL + '/prphoto.png'} style={prof} alt="ProfilePhoto" />
          </div>

          <div className="info-row">
            <div className="info-label">Username :</div>
            <div className="info-action specialo">
              Change Username
              <span className="linee"></span>
            </div>
          </div>

          <div className="info-row">
            <div className="info-label">E-mail :</div>
            <div className="info-action specialo">
              Change E-mail
              <span className="linee"></span>
            </div>
          </div>

          <div className="info-row">
            <div className="info-label">Password :</div>
            <div className="info-action specialo">
              Change Password
              <span className="linee"></span>
            </div>
          </div>

          <div className="info-row">
            <div className="info-label">Quizzes Done :</div>
            <div className="info-action"></div>
          </div>

          <div className="info-row">
            <div className="info-label">Best Quiz :</div>
            <div className="info-action"></div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Profile;
