
import React from 'react';
import { useEffect, useState } from 'react';
import './JustRegister.css';
import { Link } from 'react-router-dom';


function LogRegPage() {


  useEffect(() => {
    document.title = "Register";         // setting the page's title
  }, []);
  

  const [bgColor, setBgColor] = useState('#191a1a');
  const [modeimg, setmode] = useState(process.env.PUBLIC_URL + '/sun1.png');
  const [home, sethome] = useState('white');
  const [mid, setmid] = useState('#242525');


  const handleClick = () => {
    setBgColor(bgColor === '#191a1a' ? 'white' : '#191a1a');
    setmode(modeimg === process.env.PUBLIC_URL + '/sun1.png' ? process.env.PUBLIC_URL + '/moon1.png' : process.env.PUBLIC_URL + '/sun1.png');  
    sethome(home === 'white' ? 'black' : 'white'); 
    setmid(mid === '#242525' ? '#3f5469' : '#242525'); 
  };


  const [isHovered1, setIsHovered1] = useState(false);            // for hovering the buttons
  const [isHovered2, setIsHovered2] = useState(false);
  const [isHovered3, setIsHovered3] = useState(false);

  const [isHovered5, setIsHovered5] = useState(false);           // for hovering the home and light mode buttons
  const [isHovered6, setIsHovered6] = useState(false);


  const thebuttons = {
    width: '400px',
    height: '52px', 
    boxSizing: 'border-box',
    border: 'none', 
    borderRadius: '5px',
    fontFamily: '"Lucida Console", "Courier New", monospace',             // the sign up and sign in buttons
    padding: '12px 20px', 
    textAlign: 'center',
    cursor: 'pointer', 
    margin: '10px 0',
  };


  // for hovering the buttons


  const thebuttonshover1 = {
    opacity: isHovered1 ? '0.90' : '0.95',
    backgroundColor: 'white',
    color: 'black',
    fontSize: '16px'
  }

  const comb1 = {...thebuttons, ...thebuttonshover1}

  

  const thebuttonshover2 = {
    opacity: isHovered2 ? '0.90' : '0.95',
    backgroundColor: 'white',
    color: 'black',
    fontSize: '16px'
  }

  const comb2 = {...thebuttons, ...thebuttonshover2}



  const thebuttonshover3 = {
    opacity: isHovered3 ? '0.90' : '0.95',
    backgroundColor: 'white',
    color: 'black',
    fontSize: '16px'
  }

  const comb3 = {...thebuttons, ...thebuttonshover3}


  const imageStyle = {
    width: '36px',
    height: '20px',
    verticalAlign: 'middle',
    marginRight: '28px'
  };

  const imageStyle1 = {
    width: '32px',
    height: '32px',
    verticalAlign: 'middle',
    marginRight: '28px'
  };

  const imageStyle2 = {
    width: '28px',
    height: '28px',
    verticalAlign: 'middle',
    marginRight: '28px'
  };

  const homebtn={
    position : 'absolute',
    top : '18px',
    left :'18px',
    height : '40px',
    width : '185px',
    opacity : isHovered5 ? '0.8' : '0.9'
  };

  const modebtn={
    position : 'absolute',
    top : '18px',
    right :'18px',
    height : '45px',
    width : '45px',
    cursor : 'pointer',
    opacity : isHovered6 ? '0.8' : '0.9'
  };
  


  
  return (

    <div id="body1" style={{ backgroundColor: bgColor }}>

      <Link to="/fdquiz">
        <div id="homediv" style={homebtn} onMouseEnter={() => setIsHovered5(true)} onMouseLeave={() => setIsHovered5(false)}>     {/* the home button */}
          <span id="spanx1">fd</span>                                 
          <span id="spanx2" style={{color : home}}>.quiz</span>
        </div>
      </Link>

      <img src={modeimg} style={modebtn} alt="SetDayMode" onMouseEnter={() => setIsHovered6(true)} onMouseLeave={() => setIsHovered6(false)} onClick={handleClick}/>

      <div id="midcontainer1" style={{backgroundColor: mid}}>

        <Link to="/emailreg">
          <button id="emailbutton" style={comb1} onMouseEnter={() => setIsHovered1(true)} onMouseLeave={() => setIsHovered1(false)} >
            <img src={process.env.PUBLIC_URL + '/mail2.png'} alt="Email Logo" style={imageStyle} />
              Continue with E-mail 
            </button>
        </Link>
        
        <button id="googlebutton" style={comb2} onMouseEnter={() => setIsHovered2(true)} onMouseLeave={() => setIsHovered2(false)} >
            <img src={process.env.PUBLIC_URL + '/google3.png'} alt="Google Logo" style={imageStyle1} />
            Continue with Google 
        </button>


        <button id="facebookbutton" style={comb3} onMouseEnter={() => setIsHovered3(true)} onMouseLeave={() => setIsHovered3(false)} >
            <img src={process.env.PUBLIC_URL + '/fb69.png'} alt="Facebook Logo" style={imageStyle2} />
            Continue with Facebook
        </button>

      </div>

    </div>

  );
}

export default LogRegPage;
