
import React from 'react';
import { useEffect, useState } from 'react';
import './ContactPage.css';
import { Link } from 'react-router-dom';


function ContactPage() {


  useEffect(() => {
    document.title = "Contact Us";         // setting the page's title
  }, []);


  const [bgColor, setBgColor] = useState('#191a1a');
  const [modeimg, setmode] = useState(process.env.PUBLIC_URL + '/sun1.png');
  const [modeimg1, setmode1] = useState(process.env.PUBLIC_URL + '/profileday.png');
  const [home, sethome] = useState('white');
  const [mid, setmid] = useState('#242525');


  const handleClick = () => {
    setBgColor(bgColor === '#191a1a' ? 'white' : '#191a1a');
    setmode(modeimg === process.env.PUBLIC_URL + '/sun1.png' ? process.env.PUBLIC_URL + '/moon1.png' : process.env.PUBLIC_URL + '/sun1.png');  
    setmode1(modeimg1 === process.env.PUBLIC_URL + '/profileday.png' ? process.env.PUBLIC_URL + '/profileday.png' : process.env.PUBLIC_URL + '/profileday.png');
    sethome(home === 'white' ? 'black' : 'white'); 
    setmid(mid === '#242525' ? '#3f5469' : '#242525'); 
  };

  const [isHovered01, setIsHovered01] = useState(false);
  const [isHovered1, setIsHovered1] = useState(false);            // for hovering the 4 buttons

  const [isHovered5, setIsHovered5] = useState(false);           // for hovering the home and light mode buttons
  const [isHovered6, setIsHovered6] = useState(false);


  const thebuttons = {
    width: '400px',
    height: '52px', 
    boxSizing: 'border-box',
    border: 'none', 
    borderRadius: '5px',
    fontFamily: '"Lucida Console", "Courier New", monospace',             // the sign up and sign in buttons
    padding: '12px 20px', 
    textAlign: 'center',
    cursor: 'pointer', 
    margin: '10px 0',
  };


  // for hovering the buttons


  const thebuttonshover1 = {
    opacity: isHovered1 ? '0.85' : '0.9',
    backgroundColor: 'rgb(255, 102, 0)',
    color: 'white',
    fontSize: '20px'
  }

  const comb1 = {...thebuttons, ...thebuttonshover1}

  
  const homebtn={
    position : 'absolute',
    top : '18px',
    left :'18px',
    height : '40px',
    width : '185px',
    opacity : isHovered5 ? '0.8' : '0.9'
  };

  const modebtn={
    position : 'absolute',
    top : '18px',
    right :'18px',
    height : '45px',
    width : '45px',
    cursor : 'pointer',
    opacity : isHovered6 ? '0.8' : '0.9'
  };


  const prof = {
    opacity : isHovered01 ? '0.8' : '0.95',                 // for the profile button
    position: 'absolute',
    top: '17px', 
    left: '50%',
    transform: 'translateX(-50%)',
    height: '57px',
    width: '57px',
    cursor : 'pointer'
  }

  

  return (

    <div id="body" style={{ backgroundColor: bgColor }}>

      <Link to="/fdquiz">
        <div id="homediv" style={homebtn} onMouseEnter={() => setIsHovered5(true)} onMouseLeave={() => setIsHovered5(false)}>     {/* the home button */}
          <span id="spanx1">fd</span>                                 
          <span id="spanx2" style={{color : home}}>.quiz</span>
        </div>
      </Link>

      <img src={modeimg} style={modebtn} alt="SetDayMode" onMouseEnter={() => setIsHovered6(true)} onMouseLeave={() => setIsHovered6(false)} onClick={handleClick}/>

      <div id="midcontainerx" style={{backgroundColor: mid}}>

        <Link to="/myprofile">
                    <div>
                        <img src={modeimg1}  style={prof} alt="GoToProfile" onMouseEnter={() => setIsHovered01(true)} onMouseLeave={() => setIsHovered01(false)} />
                    </div>
        </Link>

        <form>
          <input type="text" name="name" placeholder=" Name "/><br />
          <input type="text" name="subject" placeholder=" Subject "/><br />
          <input type="email" name="email" placeholder=" E-mail "/><br /><br />
          <input type="text" name="message" placeholder=" Message " style={{ height: '150px' }} /><br /><br />
          <input type="submit" id="send" value="Send" style={comb1} onMouseEnter={() => setIsHovered1(true)} onMouseLeave={() => setIsHovered1(false)} />
          <br /><br />
        </form>

      </div>

    </div>

  );
}

export default ContactPage;
