import React, {useState} from 'react';
import './MainPage.css';
import { Link } from 'react-router-dom';

function MainPage() {

  // for the animation

  const text = 'fd.quiz';

  const [bgColor, setBgColor] = useState('#191a1a');
  const [modeimg, setmode] = useState(process.env.PUBLIC_URL + '/sun1.png');
  

  const animatedText = text.split('').map((char, index) => {
    const isOrange = char === 'f' || char === 'd';
    const isMoon = modeimg === process.env.PUBLIC_URL + '/moon1.png';
    const style = {
      animationDelay: `${index * 0.15}s`,
      color: isOrange ? 'rgb(255, 102, 0)' : (isMoon ? 'black' : 'white'),      // for applying correct color to the text
    };
    return (
      <span key={index} style={style}>
        {char}
      </span>
    );
  });


  const handleClick = () => {
    setBgColor(bgColor === '#191a1a' ? 'white' : '#191a1a');
    setmode(modeimg === process.env.PUBLIC_URL + '/sun1.png' ? process.env.PUBLIC_URL + '/moon1.png' : process.env.PUBLIC_URL + '/sun1.png');  
  };



  const [isHovered1, setIsHovered1] = React.useState(false);            // for hovering the buttons
  const [isHovered2, setIsHovered2] = React.useState(false);
  const [isHovered3, setIsHovered3] = React.useState(false);

  const [isHovered6, setIsHovered6] = React.useState(false);           // for hovering the light mode button


  const headerpr = {
    fontFamily: '"Lucida Console", "Courier New", monospace',           // the header
    fontSize: '11em',
    textAlign: 'center',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100%',
    margin: '0',
    cursor: 'default',
    position: 'relative',
    overflow: 'hidden'
  };

  const flexcon = {
    position: 'relative',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    zIndex: '1'
  };

  const divimg = {
    display: 'flex',
    justifyContent: 'center',
    marginTop: '25px',
    opacity :'0',
    animation: 'fadeIn2 3.5s forwards',
    animationDelay: '1.5s'
  };


  // style for the 3 buttons

  const iconstyle1 = {
    width: '58px',
    height: '58px',
    opacity: isHovered1 ? '0.7' : '0.8',
    cursor: 'pointer',
  };

  const iconstyle2 = {
    width: '58px',
    height: '58px',
    opacity: isHovered2 ? '0.7' : '0.8',
    cursor: 'pointer'
  };

  const iconstyle3 = {
    width: '58px',
    height: '58px',
    opacity: isHovered3 ? '0.65' : '0.8',
    cursor: 'pointer'
  };

  const modebtn={
    position : 'absolute',
    top : '18px',
    right :'18px',
    height : '45px',
    width : '45px',
    cursor : 'pointer',
    opacity: isHovered6 ? '0.8' : '0.9'
  };
  

  return (


    <div id="pag-welcome"  style={{ backgroundColor: bgColor }} >

      <img src={modeimg} style={modebtn} alt="SetDayMode" onMouseEnter={() => setIsHovered6(true)} onMouseLeave={() => setIsHovered6(false)}  onClick={handleClick} />                 

      <div style={flexcon}>

        <h1 style={headerpr}>
          <span className="text-container">
            {animatedText}
          </span>
        </h1>

        <div style={divimg}>

          <div className="icon-container" onMouseEnter={() => setIsHovered1(true)} onMouseLeave={() => setIsHovered1(false)}>
            <Link to="/login">
              <img src={process.env.PUBLIC_URL + '/login.png'} style={iconstyle1} alt="Login/Register" />
            </Link>
            <div className="hover-text">Login/Register</div>
          </div>

          <div className="icon-container" onMouseEnter={() => setIsHovered2(true)} onMouseLeave={() => setIsHovered2(false)}>
            <Link to="/contact">
              <img src={process.env.PUBLIC_URL + '/proba2.png'} style={iconstyle2} alt="Contact"/>
            </Link>
            <div className="hover-text">Contact</div>
          </div>

          <div className="icon-container" onMouseEnter={() => setIsHovered3(true)} onMouseLeave={() => setIsHovered3(false)}>
            <Link to="/about">
              <img src={process.env.PUBLIC_URL + '/about2.png'} style={iconstyle3} alt="About" />
            </Link>
            <div className="hover-text">About</div>
          </div>

        </div>

      </div>

    </div>

  );
}

export default MainPage;
