import React, { useState, useEffect } from "react";
import './testpage.css';
import { Link } from 'react-router-dom';
import CodeEditor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs';
import 'prismjs/components/prism-jsx.min';
import 'prismjs/themes/prism-okaidia.css';

function TestPage() {

  useEffect(() => {
    document.title = "About"; // setting the page's title
  }, []);

  const [code, setCode] = useState(`
    public class Vehicle {
      public void move() {
        System.out.println("The vehicle moves");
      }
    }   

    public class Car extends Vehicle {
      public void move() {
        System.out.println("The car moves");
      }
    }

    public class Main {
      public static void main(String[] args) {
        Vehicle vehicle = new Car();
        vehicle.move();
      }
    }
  `);

  const [bgColor, setBgColor] = useState('#191a1a');
  const [modeimg, setmode] = useState(process.env.PUBLIC_URL + '/sun1.png');
  const [modeimg1, setmode1] = useState(process.env.PUBLIC_URL + '/profileday.png');
  const [home, sethome] = useState('white');
  const [mid, setmid] = useState('#242525');
  const [varmode, setvarmode] = useState('white');
  const [qcolor, setqcolor] = useState('yellow');

  const handleClick = () => {
    setBgColor(bgColor === '#191a1a' ? 'white' : '#191a1a');
    setmode(modeimg === process.env.PUBLIC_URL + '/sun1.png' ? process.env.PUBLIC_URL + '/moon1.png' : process.env.PUBLIC_URL + '/sun1.png');
    setmode1(modeimg1 === process.env.PUBLIC_URL + '/profileday.png' ? process.env.PUBLIC_URL + '/profileday.png' : process.env.PUBLIC_URL + '/profileday.png');
    sethome(home === 'white' ? 'black' : 'white');
    setmid(mid === '#242525' ? '#3f5469' : '#242525');
    setvarmode(varmode === 'white' ? 'black' : 'white');
    setqcolor(qcolor === 'yellow' ? 'MediumBlue' : 'yellow');
  };

  const [isHovered01, setIsHovered01] = useState(false);
  const [isHovered5, setIsHovered5] = useState(false); // for hovering the home and light mode buttons
  const [isHovered6, setIsHovered6] = useState(false);

  const homebtn = {
    position: 'absolute',
    top: '18px',
    left: '18px',
    height: '40px',
    width: '185px',
    opacity: isHovered5 ? '0.8' : '0.9',
  };

  const modebtn = {
    position: 'absolute',
    top: '18px',
    right: '18px',
    height: '45px',
    width: '45px',
    cursor: 'pointer',
    opacity: isHovered6 ? '0.8' : '0.9',
  };

  const prof = {
    opacity: isHovered01 ? '0.8' : '0.95', // for the profile button
    position: 'absolute',
    top: '17px',
    left: '50%',
    transform: 'translateX(-50%)',
    height: '57px',
    width: '57px',
    cursor: 'pointer',
    marginBottom: '100px',
  };

  // for the quiz answer variants
  const Question = {
    fontFamily: '"Lucida Console", "Courier New", monospace',
    color: varmode,
    fontSize: '30px',
    marginTop: '55px',
  };

  return (
    <div id="maindiv" style={{ backgroundColor: bgColor }}>
      <Link to="/fdquiz">
        <div id="homediv" style={homebtn} onMouseEnter={() => setIsHovered5(true)} onMouseLeave={() => setIsHovered5(false)}>
          {/* the home button */}
          <span id="spanx1">fd</span>
          <span id="spanx2" style={{ color: home }}>.quiz</span>
        </div>
      </Link>

      <img
        src={modeimg}
        style={modebtn}
        alt="SetDayMode"
        onMouseEnter={() => setIsHovered6(true)}
        onMouseLeave={() => setIsHovered6(false)}
        onClick={handleClick}
      />

      <Link to="/myprofile">
        <div style={{ marginBottom: '47px' }}>
          <img
            src={modeimg1}
            style={prof}
            alt="GoToProfile"
            onMouseEnter={() => setIsHovered01(true)}
            onMouseLeave={() => setIsHovered01(false)}
          />
        </div>
      </Link>

      <div id="midcontainerrr" style={{ backgroundColor: mid }}>
            <CodeEditor
            value={code}
            onValueChange={(newCode) => setCode(newCode)}
            highlight={(newCode) => highlight(newCode, languages.jsx, 'jsx')}
            padding={10}
            style={{
                fontFamily: '"Fira code", "Fira Mono", monospace',
                fontSize: 18,
                border: '1px solid #333',
                borderRadius: '4px',
                backgroundColor: '#1e1e1e', // Dark background color
                color: '#ffffff', // Light text color
                height: '335px',
                width: '800px',
                overflow: 'auto',
            }}
            readOnly={true}
            />
      </div>

      <div style={Question}>
        <div id="Q">
          What does the
          <span style={{color : qcolor}}> code output </span>?
          <span style={{ color: 'orangered' }}> Click </span>
          on the
          <span style={{ color: 'green' }}> correct </span>
          variant:
        </div>

        <div id="A" className="varspan">
          "The vehicle moves"
        </div>

        <div id="B" className="varspan">
          "The vehicle moves"
        </div>

        <div id="C" className="varspan">
          The code does not compile.
        </div>

        <div id="D" className="varspan">
          None of the above.
        </div>
      </div>
    </div>
  );
}

export default TestPage;
