import React, {useState, useEffect} from "react";
import './AboutPage.css';
import { Link } from 'react-router-dom';

function AboutPage()
{

    useEffect(() => {
        document.title = "About";         // setting the page's title
    }, []);



  const [bgColor, setBgColor] = useState('#191a1a');
  const [modeimg, setmode] = useState(process.env.PUBLIC_URL + '/sun1.png');
  const [modeimg1, setmode1] = useState(process.env.PUBLIC_URL + '/profileday.png');
  const [home, sethome] = useState('white');
  const [mid, setmid] = useState('#242525');


  const handleClick = () => {
    setBgColor(bgColor === '#191a1a' ? 'white' : '#191a1a');
    setmode(modeimg === process.env.PUBLIC_URL + '/sun1.png' ? process.env.PUBLIC_URL + '/moon1.png' : process.env.PUBLIC_URL + '/sun1.png'); 
    setmode1(modeimg1 === process.env.PUBLIC_URL + '/profileday.png' ? process.env.PUBLIC_URL + '/profileday.png' : process.env.PUBLIC_URL + '/profileday.png'); 
    sethome(home === 'white' ? 'black' : 'white'); 
    setmid(mid === '#242525' ? '#3f5469' : '#242525'); 
  };



    const [isHovered01, setIsHovered01] = useState(false);
    const [isHovered2, setIsHovered2] = useState(false);                // for hovering the linkedin button

    const [isHovered5, setIsHovered5] = React.useState(false);           // for hovering the home and light mode buttons
    const [isHovered6, setIsHovered6] = React.useState(false);

    const thebuttons = {
        width: '400px',
        height: '52px', 
        boxSizing: 'border-box',
        border: 'none', 
        borderRadius: '5px',
        fontFamily: '"Lucida Console", "Courier New", monospace',             // the sign up and sign in buttons
        padding: '12px 20px', 
        textAlign: 'center',
        cursor: 'pointer', 
        margin: '10px 0',
    };


    const thebuttonshover2 = {
        opacity: isHovered2 ? '0.90' : '0.95',
        backgroundColor: 'white',
        color: 'black',
        fontSize: '16px'
    }
    
    const comb2 = {...thebuttons, ...thebuttonshover2};

    const imageStyle1 = {
        width: '32px',
        height: '32px',
        verticalAlign: 'middle',
        marginRight: '27px'
    };



    const homebtn={
        position : 'absolute',
        top : '18px',
        left :'18px',
        height : '40px',
        width : '185px',
        opacity : isHovered5 ? '0.8' : '0.9'
    };
    
    const modebtn={
        position : 'absolute',
        top : '18px',
        right :'18px',
        height : '45px',
        width : '45px',
        cursor : 'pointer',
        opacity : isHovered6 ? '0.8' : '0.9'
    };
      

    const prof = {
        opacity : isHovered01 ? '0.8' : '0.95',                 // for the profile button
        position: 'absolute',
        top: '17px', 
        left: '50%',
        transform: 'translateX(-50%)',
        height: '57px',
        width: '57px',
        cursor : 'pointer'
    }
      


    return(

        <div id="maindiv"  style={{ backgroundColor: bgColor }} >

            <Link to="/fdquiz">
                <div id="homediv" style={homebtn} onMouseEnter={() => setIsHovered5(true)} onMouseLeave={() => setIsHovered5(false)}>     {/* the home button */}
                <span id="spanx1">fd</span>                                 
                <span id="spanx2" style={{color : home}}>.quiz</span>
                </div>
            </Link>

            <img src={modeimg} style={modebtn} alt="SetDayMode" onMouseEnter={() => setIsHovered6(true)} onMouseLeave={() => setIsHovered6(false)} onClick={handleClick}/>

            <div id="midcontainerrr" style={{backgroundColor: mid}}>

                <Link to="/myprofile">
                    <div>
                        <img src={modeimg1}  style={prof} alt="GoToProfile" onMouseEnter={() => setIsHovered01(true)} onMouseLeave={() => setIsHovered01(false)} />
                    </div>
                </Link>

                <div id="div20">
                    A small personal project, created by a junior developer, that aims to provide an online platform for taking code quizzes.
                    <br/><br/>
                    <span id="spanu">Still in progres ... </span>
                </div>

                <br/><br/>


                <a  href="https://www.linkedin.com/in/florin-drutu-6014a8241/"
                    target="_blank"
                    rel="noopener noreferrer">

                    <button id="linkedinbutton" style={comb2} onMouseEnter={() => setIsHovered2(true)} onMouseLeave={() => setIsHovered2(false)} >
                        <img src={process.env.PUBLIC_URL + '/linkedin.png'} alt="Linkedin Logo" style={imageStyle1} />
                        My LinkedIn
                    </button>

                </a>

            </div>

        </div>

    );
}

export default AboutPage;