
import React from 'react';
import { useEffect, useState } from 'react';
import './LogRegPage.css';
import { Link } from 'react-router-dom';


function LogRegPage() {


  useEffect(() => {
    document.title = "Sign In";         // setting the page's title
  }, []);


  const [bgColor, setBgColor] = useState('#191a1a');
  const [modeimg, setmode] = useState(process.env.PUBLIC_URL + '/sun1.png');
  const [home, sethome] = useState('white');
  const [mid, setmid] = useState('#242525');
  const [sub,setsub] = useState('#6b0fc7');


  const handleClick = () => {
    setBgColor(bgColor === '#191a1a' ? 'white' : '#191a1a');
    setmode(modeimg === process.env.PUBLIC_URL + '/sun1.png' ? process.env.PUBLIC_URL + '/moon1.png' : process.env.PUBLIC_URL + '/sun1.png');  
    sethome(home === 'white' ? 'black' : 'white'); 
    setmid(mid === '#242525' ? '#3f5469' : '#242525'); 
    setsub(sub === '#6b0fc7' ? '#0099ff' : '#6b0fc7'); 
  };


  const [isHovered1, setIsHovered1] = useState(false);            // for hovering the 4 buttons
  const [isHovered2, setIsHovered2] = useState(false);
  const [isHovered3, setIsHovered3] = useState(false);
  const [isHovered4, setIsHovered4] = useState(false);

  const [isHovered5, setIsHovered5] = useState(false);           // for hovering the home and light mode buttons
  const [isHovered6, setIsHovered6] = useState(false);


  const thebuttons = {
    width: '400px',
    height: '52px', 
    boxSizing: 'border-box',
    border: 'none', 
    borderRadius: '5px',
    fontFamily: '"Lucida Console", "Courier New", monospace',             // the sign up and sign in buttons
    padding: '10px 20px', 
    textAlign: 'center',
    cursor: 'pointer', 
    margin: '10px 0'
  };


  // for hovering the buttons


  const thebuttonshover1 = {
    opacity: isHovered1 ? '0.85' : '0.9',
    backgroundColor: 'rgb(255, 102, 0)',
    color: 'white',
    fontSize: '20px'
  }

  const comb1 = {...thebuttons, ...thebuttonshover1}

  

  const thebuttonshover2 = {
    opacity: isHovered2 ? '0.9' : '1.0',
    backgroundColor: '#6b0fc7',
    color: 'white',
    fontSize: '20px'
  }

  const comb2 = {...thebuttons, ...thebuttonshover2}



  const thebuttonshover3 = {
    opacity: isHovered3 ? '0.9' : '0.95',
    backgroundColor: 'white',
    color: 'black',
    fontSize: '17px'
  }

  const comb3 = {...thebuttons, ...thebuttonshover3}



  const thebuttonshover4 = {
    opacity: isHovered4 ? '0.9' : '0.95',
    backgroundColor: 'white',
    color: 'black',
    fontSize: '17px'
  }

  const comb4 = {...thebuttons, ...thebuttonshover4}


  const imageStyle = {
    width: '32px',
    height: '32px',
    verticalAlign: 'middle',
    marginRight: '28px'
  };

  const imageStyle2 = {
    width: '28px',
    height: '28px',
    verticalAlign: 'middle',
    marginRight: '28px'
  };

  const homebtn={
    opacity : isHovered5 ? '0.8' : '0.9'
  };

  const modebtn={
    position : 'absolute',
    top : '18px',
    right :'18px',
    height : '45px',
    width : '45px',
    cursor : 'pointer',
    opacity : isHovered6 ? '0.8' : '0.9'
  };
  

  return (

    <div id="body" style={{ backgroundColor: bgColor }}>


      <Link to="/fdquiz">
        <div id="homediv" style={homebtn} onMouseEnter={() => setIsHovered5(true)} onMouseLeave={() => setIsHovered5(false)}>     {/* the home button */}
          <span id="spanx1">fd</span>                                 
          <span id="spanx2" style={{color : home}}>.quiz</span>
        </div>
      </Link>


      <img src={modeimg} style={modebtn} alt="SetDayMode" onMouseEnter={() => setIsHovered6(true)} onMouseLeave={() => setIsHovered6(false)} onClick={handleClick}/>



        <div id="midcontainer2" style={{backgroundColor: mid}}>

          <form>
            <input type="text" name="email" placeholder=" Username or E-mail "/><br />
            <input type="password" name="pwd" minLength="8" placeholder=" Password "/><br />
            <input type="submit" id="loginbutton" value="Sign In" style={comb1} onMouseEnter={() => setIsHovered1(true)} onMouseLeave={() => setIsHovered1(false)} />
            <br /><br />
          </form>

          <Link to="/recoverpassword">
            <div class="special">
              <span id="span01">Forgot</span>
              <span id="span02"> your password ?</span>
            </div>
          </Link>

          <br /><br />

          <button id="googlebutton" style={comb3} onMouseEnter={() => setIsHovered3(true)} onMouseLeave={() => setIsHovered3(false)} >
              <img src={process.env.PUBLIC_URL + '/google3.png'} alt="Google Logo" style={imageStyle} />
              Continue with Google 
          </button>


          <button id="facebookbutton" style={comb4} onMouseEnter={() => setIsHovered4(true)} onMouseLeave={() => setIsHovered4(false)} >
              <img src={process.env.PUBLIC_URL + '/fb69.png'} alt="Facebook Logo" style={imageStyle2} />
              Continue with Facebook
          </button>

          <br /><br /><br /><br />

          <div id="text01">
            <span id="span01">&nbsp;new</span>
            <span id="span02"> around here ?</span>
          </div>

          <br />

          <Link to="/register">
            <button id="signupbutton" style={{...comb2, backgroundColor : sub}} onMouseEnter={() => setIsHovered2(true)} onMouseLeave={() => setIsHovered2(false)} >Sign Up</button>
          </Link>


        </div>


    </div>

  );
}

export default LogRegPage;
